import Vue from 'vue'
import Router from 'vue-router'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import helloWorld from "@/components/HelloWorld";
Vue.use(Router)
const routes=[

    // {
    // path: '/',
    // component:{LoginBegin},
    // },


    {
      path:'/',
      redirect:'/login',
    },
    {
        path: '/login',
        component:() =>  import('../pages/LoginBegin')
    },
    {
        path: '/main',
        component:() =>  import('../pages/MainInterface')
    },
    {
        path: '/register',
        component:() =>  import('../pages/Register')
    },
    {
        path: '/ormanage',
        component:() =>  import('../pages/ormanage')
    },
    {
        path: '/ordercanceled',
        component:() =>  import('../pages/ordercanceled')
    },
    {
        path: '/orderwillcancel',
        component:() =>  import('../pages/orderwillcancel')
    },
    {
        path: '/ordercheckedin',
        component:() =>  import('../pages/ordercheckedin')
    },
    {
        path: '/orderwaitpay',
        component:() =>  import('../pages/orderwaitpay')
    },
    {
        path: '/personinfo',
        component:() =>  import('../pages/personinfo')
    },
    {
        path: '/changeinfo',
        component:() =>  import('../pages/changeinfo')
    },
    {
        path: '/mapmode',
        component:() =>  import('../pages/mapmode')
    },
    // {path: "",component:LoginBegin}
]

const router= new Router({
    // mode:'history',
    routes
})

export default router